@import "../../assets/fonts/fonts.css";

/*
Font variables
-------------------------
*/

$font_regular: "Open Sans Regular", sans-serif;
$font_extrabold: "Open Sans Extrabold", sans-serif;
$font_bold: "Open Sans Bold", sans-serif;
$font_semibold: "Open Sans Semibold", sans-serif;
$font_light: "Open Sans Light", sans-serif;

/*
Breakpoints
------------------------- 
*/

$breakpoint_mobile_max: 991px;
$breakpoint_desktop: 992px;
$breakpoint_desktop_lg: 1200px;
$breakpoint_desktop_xl: 1600px;
$breakpoint_desktop_xxl: 2000px;

/*
Vars
------------------------- 
*/

$body_font_size: 14;

/*
Color vars
------------------------- 
*/

$color_white: #ffffff;
$color_black: #000000;

$color_grey1: #333333;
$color_grey2: #555555;

/*
Background colors
----------------------------------------
*/

.bc-white {
    background-color: $color_white !important;
}
.bc-black {
    background-color: $color_black !important;
}

.bc-grey2 {
    background-color: $color_grey2 !important;
}

/*
Text colors
----------------------------------------
*/

.c-white {
    color: $color_white !important;
}
.c-black {
    color: $color_black !important;
}

.c-grey2 {
    color: $color_grey2 !important;
}

/*
Shadow
----------------------------------------
*/

.shad-s1 {
    box-shadow: 0px 0px 0.2143rem rgba(0,0,0,0.4);
}

/*
Size
----------------------------------------
*/

.abs-full {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.h-full {
    height: 100%;
}
.w-full {
    width: 100%;
}
.hw-full {
    height: 100%;
    width: 100%;
}

.vh-full {
    min-height: 100vh;
}

/*
Layout
----------------------------------------
*/

.container-narrow {
    max-width: 50rem;
    margin: 0 auto;
}

/*
List
----------------------------------------
*/

ul.ns, ol.ns {
    list-style: none;
    padding: 0;
    margin: 0;
}
ul.ns li, ol.ns li {
    padding: 0;
    margin: 0;
}

/*
Text
----------------------------------------
*/

.t-em {
    font-style: italic;
    line-height: 1.4em;
}
.t-uc {
    text-transform: uppercase;
}

.ta-left {
    text-align: left !important;
}
.ta-center {
    text-align: center !important;
}
.ta-right {
    text-align: right !important;
}

/*
Responsive hide
----------------------------------------
*/

@media (max-width: 991px)
{
    body:not(.wp-admin) .hide-mobile {
        display: none !important;
    }
}
@media (min-width: 992px)
{
    body:not(.wp-admin) .hide-desktop, 
    body:not(.wp-admin) .hs-hide-desktop {
        display: none !important;
    }
}

/*
Background image
----------------------------------------
*/

.bg-img-contain {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}
.bg-img-cover {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.img-fluid {
    max-width: 100%;
    height: auto;
    /* object-fit: cover; */
}

/*
Responsive image
----------------------------------------
*/

.img-resp {
    position: relative;
}
.img-resp img {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.img-resp.img-cover img {
    object-fit: cover;
}
.img-resp.img-contain img {
    object-fit: contain;
}

/*
Responsive rectangles
----------------------------------------
*/

.rect-100-100 {
    width: 100%;
}
.rect-100-100::before {
    content: "";
    display: block;
    padding-bottom: 100%;
}

.rect-150-100 {
    width: 100%;
}
.rect-150-100::before {
    content: "";
    display: block;
    padding-bottom: 67%;
}

/*
Bootstrap icon
----------------------------------------
*/

.bi-before::before {
    font-family: "bootstrap-icons";
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    // vertical-align: -.125em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.bi-after::after {
    font-family: "bootstrap-icons";
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    // vertical-align: -.125em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/*
form
----------------------------------------
*/

form {

    input[type='text'], 
    input[type='email'], 
    input[type='number'], 
    input[type='tel'],
    input[type='password'],
    textarea {
        display: block;
        width: 100%;
        padding: 0.75em 1.25em;
        font-family: $font_light;
        font-size: 1rem;
        line-height: 1.4em;
        margin: 0;
        border: 2px solid $color_grey2;
        border-radius: 0.2143rem;
    }

    textarea {
        resize: vertical;
    }

    .view.form-nice-dropdown {
        font-size: inherit;
        .selected-label {
            border: 2px solid $color_grey2;
            border-radius: 0.2143rem;
            box-shadow: none;
        }
    }

    &.shad-inputs {
        input[type='text'], 
        input[type='email'], 
        input[type='number'], 
        input[type='tel'],
        input[type='password'],
        textarea {
            border: none;
            box-shadow: 0px 0px 0.2143rem rgba(0,0,0,0.4);            
        }

        .view.form-nice-dropdown {
            .selected-label {
                border: none;
                box-shadow: 0px 0px 0.2143rem rgba(0,0,0,0.4);            
            }
        }
    }

    input.app-btn[type="file"]::file-selector-button {
        margin-right: 0.5em;
        padding-top: 0;
        padding-bottom: 0;
        border-radius: 0;
        border-top: none;
        border-right: 1px solid $color_grey2;
        border-bottom: none;
        border-left: 0;
        background-color: transparent;
        color: inherit;
        cursor: pointer;
    }

    /* Chrome, Firefox, Opera, Safari 10.1+ */
    ::placeholder {
        color: inherit;
        opacity: 1;
    }
    /* Internet Explorer 10-11 */
    :-ms-input-placeholder {
        color: inherit;
    }
    /* Microsoft Edge */
    ::-ms-input-placeholder {
        color: inherit;
    }

    label {
        display: block;
        padding: 0 0 0.25rem;
        margin: 0;
        // font-family: $font_semibold;
    }
    .input-desc {
        font-size: 0.8rem;
        line-height: 1.4em;
        color: $color_grey2;
        padding: 0 0 1rem;
    }

    .form-block {
        padding: 0 0 1.25rem;
    }
    .form-input {
        padding: 0 0 0.75rem;
    }

    .field-info {
        position: relative;
        font-size: 0.857rem;
        line-height: 1.4em;
        padding: 0.7143em 1.2857em 0.7143em 2.83em;
        color: $color_grey2;

        &::before {
            position: absolute;
            top: 0;
            left: 0;
            padding: 0.7143em 1.2857em;
            font-family: 'Font Awesome 5 Free';
            content: '\f05a';
            font-weight: 900;
        }
    }
}

/*
.nice-checkbox
----------------------------------------
*/

.nice-checkbox:not(.view) {

    font-size: 1em;
    display: inline-block;

    input {
        display: none;
    }

    label {
        display: block;
        position: relative;
        padding: 0 0 0 2.5em;
        // min-height: 1.8em;
        cursor: pointer;

        font-size: 1em;
        line-height: 1.4em;

        &:before {
            display: block;
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 1.4em;
            height: 1.4em;

            background-color: #ffffff;
            border: 2px solid $color_grey2;
        }
    }
    input:checked + label:before {
        background-color: $color_grey2;
        border-color: $color_grey2;
    }

    &.size-large {
        label {
            padding: 0.2em 0 0.2em 2.5em;
            &:before {
                width: 1.5em;
                height: 1.5em;
            }
        }
    }
}

/*
.app-btn
----------------------------------------
*/

.app-btn {
    position: relative;
    padding-top: 0.75em;
    padding-bottom: 0.75em;
    padding-left: 1.5em;
    padding-right: 1.5em;
    margin: 0;
    display: inline-block;
    font-size: 1rem;
    line-height: 1.4em;
    font-family: $font_regular;
    border: none;
    border-radius: 0.1875em;
    background-color: $color_grey2;
    color: $color_white;
    text-decoration: none;
    text-align: center;
    opacity: 1;
    box-shadow: 1px 1px 0.2143rem rgba(0,0,0,0.4);
    cursor: pointer;

    &.active, &:disabled, &:hover, &:visited {
        opacity: 0.7;
        transition-property: opacity;
        transition-duration: 0.3s;
        box-shadow: 1px 1px 0.2143rem rgba(0,0,0,0.6);
        color: $color_white;
    }
    &:disabled {
        cursor: not-allowed;
    }

    // button sizing
    &.size-small {
        font-size: 0.75em;
        line-height: 1.4em;
    }
    &.size-large {
        font-size: 1.25em;
        line-height: 1.4em;
    }
    &.size-full {
        display: block;
        width: 100%;
    }

    // button icons
    &.icon-btn {

        // &.icon-left {
            padding-left: 4em;

            > span:first-child:not(:last-child),
            > i {
                font-size: inherit;
                line-height: inherit;
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                padding-top: inherit;
                padding-bottom: inherit;
                padding-left: 1em;
                padding-right: 1em;
                background-color: rgba(0,0,0,0.2);
                color: inherit;
            }
        // }
    }
    &.icon-only {
        > i {
            font-size: inherit;
            line-height: inherit;
        }
    }
}

/*
.switch-contents
----------------------------------------
*/

.switch-contents {
    .switch-content {
        display: none;
        &.active {
            display: block;
        }
    }
}
